const { get, post, controller } = sword.Decorator;
@controller('/api/test')
class Test extends sword.Controller {
    @get('/get')
    testGet() {
        const { query } = this;
        this.success(query);
    }

    @post('/post')
    async testPost() {
        const data = await sword.service('example.find')({
            id: 1
        });
        this.success(data);
    }

    @get('/error')
    error() {
        this.throw(400, 'error');
    }
}
export default Test;
